@import "./fonts.scss";

$grey1: #333333;
$pink: #f6f0e1;
$white: #ffffff;
$yellow: #f7ca59;
$blue: #a6d7c9;
$bloomarin: #bae8db;

body {
  margin: 0;
}

.container-wrap {
  max-width: 1150px;
  margin: 0 auto;
}

.section_title {
  font: bold 58px Playfair Display;
  line-height: 72px;
  margin-top: 130px;
  text-align: center;
  text-transform: capitalize;
  color: $grey1;
}

// first section
.welcome {
  position: relative;
  padding-top: 130px;
  background: $pink;
  padding-bottom: 108px;

  .block-wrap {
    justify-content: space-between;

    @media screen and (min-width: 320px) and (max-width: 1145px) {
      justify-content: center;
    }
  }

  .welcome-service {
    overflow: hidden;
    padding-top: 30px;
  }

  h1 {
    font: 60px Playfair Display;
    line-height: 120%;
    text-align: center;
    color: $grey1;
    margin: 0;
  }

  p {
    font: 420 20px Brandon Grotesque;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.3px;
    margin-top: 40px;
    color: $grey1;
  }

  .block-wrap {
    position: relative;
    height: auto;
    margin: 33px auto 0;
    min-height: 595px;
    z-index: 0;

    .block-content {
      .block-about {
        margin-left: 52px;
        width: 1055px;
        height: 510px;
        background: $white;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 20px;
        z-index: 1;

        @media screen and (min-width: 992px) and (max-width: 1145px) {
          width: 1000px;
        }

        @media screen and (min-width: 320px) and (max-width: 1200px) {
          height: auto;
          width: 80%;
          margin: 0 auto;
        }

        .block-about_body {
          margin: 0 32px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            margin: 0 2px;
          }

          .schema_inner {
            padding-top: 60px;

            .schema_screens {
              justify-content: space-evenly;
              position: relative;
              &::after {
                content: "";
                position: absolute;
                width: 4px;
                height: 260px;
                top: -60px;
                background-color: $grey1;
              }

              .schema_body {
                .schema_title {
                  font: 900 16px "Brandon Grotesque";
                  line-height: 24px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;
                  color: $grey1;
                  margin-top: 19px;
                }
              }

              .arrow {
                padding-top: 6px;
                z-index: 2;

                @media screen and (min-width: 320px) and (max-width: 650px) {
                  display: flex;
                  justify-content: center;
                  align-items: baseline;
                  width: 50%;
                }
              }

              svg {
                @media screen and (min-width: 320px) and (max-width: 650px) {
                  width: 50%;
                  height: auto;
                }
              }
            }

            .schema_cards {
              position: relative;
              z-index: 2;
              margin-top: 35px;
              justify-content: space-between;
              @media screen and (min-width: 768px) and (max-width: 1200px) {
                justify-content: space-around;
              }
              @media screen and (min-width: 320px) and (max-width: 768px) {
                justify-content: space-between;
              }
              .schema_card {
                position: relative;
                width: 144px;
                height: 176px;
                background: $yellow;
                border-radius: 10px;

                @media screen and (min-width: 650px) and (max-width: 1200px) {
                  width: 130px;
                  height: 163px;
                }

                @media screen and (min-width: 320px) and (max-width: 650px) {
                  width: 100px;
                  height: 105px;
                }

                &:nth-child(2),
                &:nth-child(3) {
                  &:before {
                    content: "";
                    position: absolute;
                    width: 50px;
                    height: 4px;
                    left: -48px;
                    background-color: #fcbd3b;
                    z-index: -2;

                    @media screen and (min-width: 320px) and (max-width: 1200px) {
                      width: 124px;
                      height: 4px;
                      left: -119px;
                    }
                  }
                }

                &:after {
                  content: " ";
                  position: absolute;
                  width: 144px;
                  height: 176px;
                  left: 8px;
                  top: 9px;
                  z-index: -1;
                  background: $grey1;
                  border-radius: 10px;

                  @media screen and (min-width: 320px) and (max-width: 1200px) {
                    width: 130px;
                    height: 163px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 650px) {
                    width: 100px;
                    height: 105px;
                  }
                }

                .schema_title {
                  font: 900 13px Brandon Grotesque;
                  line-height: 20px;
                  margin-top: 16px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;
                  color: $grey1;

                  @media screen and (min-width: 320px) and (max-width: 650px) {
                    margin-top: 10px;
                  }
                }

                svg {
                  @media screen and (min-width: 320px) and (max-width: 650px) {
                    width: 45%;
                    height: auto;
                  }
                }
              }
            }
          }

          .list_inner {
            position: relative;
            padding-left: 70px;
            @media screen and (min-width: 320px) and (max-width: 1200px) {
              padding-left: 40px;
            }
            h3 {
              padding-top: 36px;
              font: 900 34px Brandon Grotesque;
              line-height: 48px;
              color: $grey1;
            }
            .list {
              margin-top: 32px;
              padding-left: 25px;
              .list_item {
                list-style: none;
                margin-bottom: 20px;
                position: relative;
                &:nth-child(2),
                &:nth-child(3) {
                  &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: -25px;
                    transform: translateY(-50%);
                    width: 20px;
                    height: 3px;
                    background-color: #000000;
                  }
                }
                .list_img {
                  margin-right: 13px;
                }
                .list_title {
                  font: 420 24px Brandon Grotesque;
                  line-height: 32px;
                  letter-spacing: 0.3px;
                  color: $grey1;
                }
              }
            }
            .list_line {
              position: absolute;
              top: 146px;
              bottom: 14%;
              left: 16.5%;
              width: 20px;
              border-radius: 7px 0 0 7px;
              border-left: 3px solid #000;
              border-top: 3px solid #000;
              border-bottom: 3px solid #000;

              @media screen and (min-width: 901px) and (max-width: 1200px) {
                left: 4.7%;
                top: 131px;
                bottom: 11%;
              }
              @media screen and (min-width: 864px) and (max-width: 901px) {
                left: 39px;
                top: 131px;
                bottom: 11%;
              }
              @media screen and (min-width: 706px) and (max-width: 864px) {
                left: 39px;
                top: 147px;
                bottom: 9%;
              }
              @media screen and (min-width: 320px) and (max-width: 706px) {
                left: 39px;
                top: 147px;
                bottom: 9%;
              }
            }
          }
        }

        &:after {
          content: " ";
          position: absolute;
          right: 0;
          top: 16px;
          z-index: -1;
          width: 1031px;
          height: 510px;
          background: $grey1;
          border-radius: 20px;

          @media screen and (min-width: 320px) and (max-width: 1200px) {
            height: 100%;
            width: 77%;
            margin: 0 auto;
            right: 8%;
          }
        }
      }
    }
    .block-marker {
      position: absolute;
      width: 70px;
      height: 244px;
      background: $grey1;
      border-radius: 20px 0px 0px 20px;
      left: 0;
      top: 0;
      z-index: -1;

      @media screen and (min-width: 768px) and (max-width: 1200px) {
        left: 5%;
      }
      @media screen and (min-width: 320px) and (max-width: 768px) {
        left: 3%;
      }
      p {
        color: $white;
        white-space: nowrap;
        font: 900 14px Brandon Grotesque;
        line-height: 143%;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin: 0;
        transform: rotate(-90deg) translateY(-5px);
      }
    }
  }
}

// second section
.youNeedIt {
  background-color: $blue;
  padding-bottom: 59px;
  position: relative;

  @media screen and (min-width: 320px) and (max-width: 1200px) {
    padding-bottom: 100px;
  }

  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $pink;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }

  h3 {
    font: 900 32px Brandon Grotesque;
    text-transform: uppercase;
    color: $grey1;
    padding-top: 99px;
  }

  .cards {
    margin-top: 40px;
    justify-content: space-between;

    @media screen and (min-width: 320px) and (max-width: 1200px) {
      justify-content: center;
    }

    .card_item {
      width: 445px;
      height: 128px;
      background: $bloomarin;
      border-radius: 10px;
      margin-bottom: 33px;
      @media screen and (min-width: 320px) and (max-width: 1200px) {
        margin: 15px;
      }

      h4 {
        font: 900 20px Brandon Grotesque;
        line-height: 28px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;
        margin: 0 0 0 24px;
      }
    }
  }
  .image {
    position: absolute;
    right: 2.5%;
    bottom: -23px;
    width: 237px;
    height: 222px;
    background: #fcbd3b;
    border-radius: 10px;

    @media screen and (min-width: 1270px) and (max-width: 1440px) {
      right: 1%;
    }
    @media screen and (min-width: 1200px) and (max-width: 1270px) {
      right: -3%;
    }
    @media screen and (min-width: 978px) and (max-width: 1200px) {
      bottom: -20%;
    }
    @media screen and (min-width: 320px) and (max-width: 978px) {
      bottom: -14%;
    }

    img {
      position: absolute;
      right: 14px;
      top: 12px;
    }
  }
}

// third section
.migrations {
  padding-bottom: 130px;
  .poster {
    margin-top: 186px;
    width: 100%;
    height: 745px;
    background: $pink;
    border: 6px solid #000000;
    box-sizing: border-box;
    border-radius: 20px;

    @media screen and (min-width: 320px) and (max-width: 992px) {
      width: 98%;
      margin: 200px auto 250px;
    }

  

    h3 {
      margin-top: 100px;
    }

    .poster_body {
      padding: 64px 116px 0;
      @media screen and (min-width: 768px) and (max-width: 1062px) {
        padding: 64px 50px 0;
      }
      .list {
        padding-left: 25px;

        @media screen and (min-width: 320px) and (max-width: 992px) {
          margin: 0 auto 55px;
          width: 80%;
        }
        .list_item {
          list-style: none;
          margin-bottom: 25px;
          position: relative;
          &:nth-child(2),
          &:nth-child(3) {
            &:after {
              content: "";
              position: absolute;
              top: 50%;
              left: -25px;
              transform: translateY(-50%);
              width: 20px;
              height: 3px;
              background-color: #000000;
            }
          }
          .list_img {
            margin-right: 13px;
          }
          .list_title {
            font: 420 24px Brandon Grotesque;
            line-height: 32px;
            letter-spacing: 0.3px;
            color: $grey1;
          }
        }
      }
      .list_line {
        position: absolute;
        top: 30px;
        bottom: 59.5%;
        width: 20px;
        border-radius: 7px 0 0 7px;
        border-left: 3px solid #000;
        border-top: 3px solid #000;
        border-bottom: 3px solid #000;

        @media screen and (min-width: 320px) and (max-width: 992px) {
          top: 14px;
          left: 11%;
          bottom: 26.5%;
        }
      }

      .card {
        width: 489px;
        height: 717px;
        background: #fc6839;
        border-radius: 20px;
        position: relative;

        @media screen and (min-width: 992px) and (max-width: 1062px) {
          width: 435px;
        }
        .card_body {
          padding: 50px 50px 0;

          h4 {
            font: 900 24px Brandon Grotesque;
            line-height: 32px;
            letter-spacing: 0.3px;
            color: #ffffff;
          }
          p {
            font-family: 420 20px Brandon Grotesque;
            line-height: 30px;
            margin-top: 24px;
            letter-spacing: 0.3px;
            color: #ffffff;
          }

          a {
            position: relative;
            font: bold 24px Brandon Grotesque;
            line-height: 32px;
            letter-spacing: 0.3px;
            text-decoration-line: underline;
            color: #0a0a0a;
            margin-top: 16px;
            cursor: pointer;
            z-index: 2;
          }

          .image {
            position: absolute;
            right: -1px;
            bottom: -2px;
            left: -1px;
            border-radius: 20px;
            overflow: hidden;
            z-index: 1;
            img {
              width: 100%;
            }
          }
        }
      }
    }
  }
}

.application {
  background: $bloomarin;
  padding-bottom: 145px;
  @media screen and (min-width: 992px) and (max-width: 1024px) {
    margin-bottom: 100px;
  }
  @media screen and (min-width: 320px) and (max-width: 992px) {
    margin-bottom: 0px;
  }
  h3 {
    padding-top: 170px;
    margin-top: 0;
  }
  p {
    font: 420 20px Brandon Grotesque;
    line-height: 28px;
    text-align: center;
    letter-spacing: 0.3px;
    color: $grey1;
    margin-top: 40px;
  }
  .cadrs_body {
    width: 100%;
    margin-top: 46px;

    .card {
      position: relative;
      width: 98%;
      max-width: 1107px;
      height: 280px;
      background: $pink;
      border-radius: 20px;
      margin-bottom: 41px;

      @media screen and (min-width: 600px) and (max-width: 1024px) {
        height: 300px;
      }
      @media screen and (min-width: 320px) and (max-width: 600px) {
        height: auto;
      }

      &::after {
        content: "";
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 100%);
        border: 20px solid transparent;
        border-top: 20px solid $pink;
      }

      .card_image {
        margin: 32px 0 38px;
        @media screen and (min-width: 320px) and (max-width: 768px) {
          margin: 32px 0 20px;
        }
      }

      .card_title {
        font: 900 20px Brandon Grotesque;
        line-height: 28px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;
        margin: 0 0 32px;
        text-align: center;
        @media screen and (min-width: 320px) and (max-width: 768px) {
          margin: 0 0 5px;
        }
      }
      p {
        font: 420 20px Brandon Grotesque;
        line-height: 30px;
        text-align: center;
        letter-spacing: 0.3px;
        color: $grey1;
        mix-blend-mode: normal;
        margin: 0;
        padding: 0 40px;
        @media screen and (min-width: 320px) and (max-width: 600px) {
          margin-bottom: 50px;
        }
      }
    }
  }
}

.transition {
  position: relative;
  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $bloomarin;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }
  .transition_body {
    padding-top: 162px;
    @media screen and (min-width: 320px) and (max-width: 1200px) {
      margin: 0 auto;
    }

    .transition_body__right {
      margin-left: 80px;

      @media screen and (min-width: 320px) and (max-width: 1400px) {
        margin-left: 0px;
        display: flex;
        justify-content: flex-end;
        align-items: flex-start;
      }
    }

    h3 {
      font: normal 58px Playfair Display;
      color: $grey1;
      margin-bottom: 32px;

      @media screen and (min-width: 1134px) and (max-width: 1199px) {
        text-align: center;
        margin: 0 auto 40px;
        max-width: 60%;
      }
      @media screen and (min-width: 320px) and (max-width: 1134px) {
        text-align: center;
        margin: 0 auto 40px;
        max-width: 90%;
      }
    }
    p {
      font: normal 20px Brandon Grotesque;
      line-height: 28px;
      letter-spacing: 0.3px;
      color: $grey1;
      margin-bottom: 79px;
      @media screen and (min-width: 320px) and (max-width: 1199px) {
        text-align: center;
        max-width: 90%;
        margin: 0 auto;
      }
    }

    .note {
      position: absolute;
      bottom: -25px;
      left: 34px;
      width: 257.09px;
      height: 212.59px;
      @media screen and (min-width: 870px) and (max-width: 1200px) {
        bottom: auto;
        top: 107%;
        left: 85px;
      }
      @media screen and (min-width: 320px) and (max-width: 870px) {
        bottom: 0;
        top: 0;
        left: 0;
        right: 0;
        z-index: 2;
        margin: 0 auto;
        position: relative;
      }

      img {
        position: absolute;
      }
      h5 {
        position: absolute;
        top: 25%;
        left: 27px;
        font: 900 24px Brandon Grotesque;
        line-height: 24px;
        align-items: center;
        letter-spacing: 0.3px;
        color: $grey1;
        transform: rotate(-4deg);
      }
      h6 {
        position: absolute;
        bottom: -9%;
        left: 36px;
        font: 420 14px Brandon Grotesque;
        line-height: 20px;
        letter-spacing: 0.3px;
        color: $grey1;
        transform: rotate(-4deg);
      }
    }
  }
  .principles_body {
    position: relative;
    width: 576px;
    height: 466px;
    background: $pink;
    border-radius: 20px;
    margin: 37px 0 130px;
    padding-bottom: 130px;
    @media screen and (min-width: 1024px) and (max-width: 1200px) {
      margin: 80px 20px 50px 0;
    }
    @media screen and (min-width: 644px) and (max-width: 1024px) {
      margin: 80px auto 50px;
    }
    @media screen and (min-width: 320px) and (max-width: 644px) {
      margin: 100px auto 50px;
      width: 95%;
      height: auto;
      padding-bottom: 30px;
    }
    

    .block_technical {
      position: absolute;
      top: -22px;
      left: 30px;
      @media screen and (min-width: 320px) and (max-width: 644px) {
        left: 20px;
      }
      img {
        &:first-child {
          margin-right: 58.21px;
          @media screen and (min-width: 320px) and (max-width: 644px) {
            margin-right: 15px;
          }
        }
        &:last-child {
          margin-left: 49.22px;
          @media screen and (min-width: 320px) and (max-width: 644px) {
            margin-right: 15px;
          }
        }
      }
    }

    h4 {
      padding: 157px 0 0 32px;
      font: 900 20px Brandon Grotesque;
      line-height: 28px;
      letter-spacing: 0.3px;
      text-transform: uppercase;
      color: $grey1;

      @media screen and (min-width: 320px) and (max-width: 644px) {
        text-align: center;
        padding: 157px 0 0 0;
      }
    }

    ul {
      margin-top: 40px;
      padding-left: 55px;
      @media screen and (min-width: 320px) and (max-width: 644px) {
        padding-left: 30px;
      }
      li {
        list-style: $grey1;
        font: 420 20px Brandon Grotesque;
        line-height: 30px;
        letter-spacing: 0.3px;
        color: $grey1;
        margin-bottom: 15px;
      }
    }
  }
}

.questions {
  background-color: #f3dba2;
  h3 {
    padding-top: 161px;
    margin: 0 0 72px;
  }

  .card_body {
    width: 100%;
    padding-bottom: 130px;

    @media screen and (max-width: 700px) {
      flex-direction: column;
      padding-bottom: 80px;
    }
    .card {
      width: 180px;
      height: 170px;
      background: $yellow;
      border-radius: 10px;

      @media screen and (min-width: 600px) and (max-width: 1024px) {
        width: 140px;
        height: 130px;
      }
      @media screen and (min-width: 320px) and (max-width: 991px) {
        margin: 0 auto;
      }
      

      h5 {
        font: 900 13px Brandon Grotesque;
        line-height: 20px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;
        margin-left: 23px;

        @media screen and (min-width: 700px) and (max-width: 1024px) {
          margin-left: 15px;
        }
      }
    }

    img {
      margin: 0 24px;
      @media screen and (min-width: 700px) and (max-width: 1024px) {
        margin: 0 5px;
      }
      @media screen and (max-width: 700px) {
        margin: 24px 24px;
        transform: rotate(90deg);
      }
    }
  }
}

.services {
  background: #f3dba2;
  padding-bottom: 160px;

  h3 {
    font: 900 32px Brandon Grotesque;
    line-height: 48px;
    text-transform: uppercase;
    color: $grey1;
    padding-top: 160px;
    margin-bottom: 48px;
  }

  .services_body {
    justify-content: space-between;
    @media screen and (min-width: 600px) and (max-width: 1180px) {
      justify-content: space-around;
    }
    @media screen and (max-width: 600px) {
      justify-content: center;
    }
  }

  .service_card {
    position: relative;
    width: 352px;
    height: 272px;
    padding: 32px;
    background: #f7ca59;
    border-radius: 10px;
    margin-bottom: 23px;

    @media screen and (min-width: 600px) and (max-width: 1180px) {
      margin: 0 15px 23px;
    }
    @media screen and (max-width: 768px) {
